<template>
    <div class="home">
        <div class="content">
            <p class="title">{{ $t("language.commonly") }}</p>
            <ul class="box">
                <li
                    class="item"
                    v-if="setValue.forget_login_password == 1"
                    @click="goPage('/forgot/1')"
                >
                    <img src="../assets/image/lgoin.png" class="itemImg" />
                    <span class="name">{{ $t("language.password") }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.withdrawal_not_arrived == 1"
                    @click="goPage('/payment')"
                >
                    <img src="../assets/image/help.png" class="itemImg" />
                    <span class="name">{{ $t("language.arrived") }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.account_thaw == 1"
                    @click="goPage('/forgot/5')"
                >
                    <img src="../assets/image/user.png" class="itemImg" />
                    <span class="name">{{ $t("language.unfreeze") }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.forget_withdrawal_password == 1"
                    @click="goPage('/forgot/4')"
                >
                    <img src="../assets/image/money.png" class="itemImg" />
                    <span class="name">{{ $t("language.withdrawal") }}</span>
                </li>

                <li
                    class="item"
                    v-if="setValue.update_bank_name == 1"
                    @click="goPage('/forgot/2')"
                >
                    <img src="../assets/image/account.png" class="itemImg" />
                    <span class="name">{{ $t("language.modifybank") }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.update_bank_number == 1"
                    @click="goPage('/forgot/3')"
                >
                    <img src="../assets/image/bank.png" class="itemImg" />
                    <span class="name">{{
                        $t("language.modifybankcode")
                    }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.deposit_not_arrived == 1"
                    @click="goPage('/setMoney')"
                >
                    <img src="../assets/image/rechage.png" class="itemImg" />
                    <span class="name">{{ $t("language.application") }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.complaints_suggestions == 1"
                    @click="goPage('/complaint')"
                >
                    <img src="../assets/image/text.png" class="itemImg" />
                    <span class="name">{{ $t("language.suggestions") }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.self_help_offer == 1"
                    @click="openPage"
                >
                    <img src="../assets/image/text2.png" class="itemImg" />
                    <span class="name">{{ $t("language.discount") }}</span>
                </li>
                <li
                    class="item"
                    v-if="setValue.help_center == 1"
                    @click="goPage('/problem')"
                >
                    <img src="../assets/image/text1.png" class="itemImg" />
                    <span class="name">{{ $t("language.helpCenter") }}</span>
                </li>
            </ul>
            <div class="right1">
                <div
                    class="btnDiv btnR Breathe"
                    v-if="setValue.customer == 1"
                    @click="getHelpUrl"
                >
                    <img src="../assets/image/news.png" class="imgHeader" />
                    <p class="flash">{{ $t("language.servicetow") }}</p>
                </div>
                <div
                    class="btnDiv"
                    v-if="setValue.customer_phone == 1"
                    :class="[index == 3 ? 'btnActivit1' : 'btnR']"
                    @click="goPage('/phone')"
                >
                    <img src="../assets/image/phone.png" class="imgHeader" />
                    <p class="flash">{{ $t("language.phone") }}</p>
                </div>
            </div>
            <div v-if="setValue.help_center == 1">
                <p class="title">{{ $t("language.help") }}</p>

                <ul class="ul">
                    <li
                        class="flexList"
                        v-for="(item, i) of problemList"
                        :key="i"
                    >
                        <div class="item">{{ item.questionTypeName }}</div>
                        <div class="right">
                            <p
                                v-for="(it, f) of item.faqList"
                                :key="f"
                                class="listname"
                                @click="openDateil(it)"
                            >
                                {{ it.questionTitle }}
                            </p>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    computed: {
        ...mapState({
            setValue: "service",
        }),
    },
    data() {
        return {
            problemList: [],
            index: 0,
        };
    },
    created() {
        this.$http
            .get(this.$api.customerCcenter + "customer_center")
            .then((res) => {
                console.log(res);
                if (res.code == 0) {
                    let value = res.data.svalue;
                    this.$store.commit("setData", value);
                }
            });
        this.$http.post(this.$api.getProblemList).then((res) => {
            console.log(res);
            if (res.code == 0) {
                this.problemList = res.data;
            }
        });
    },
    methods: {
        //获取24人工客服地址
        getHelpUrl() {
            this.$http.get(this.$api.getHelpUrl).then((result) => {
                console.log(result);
                if (result.code == 0) {
                    if (result.data.domain) {
                        window.open(result.data.domain, "_bank");
                    } else {
                        this.$message.warning("配置出错");
                    }
                }
            });
        },
        openDateil(data) {
            this.$store.commit("setProblems", data);
            this.$router.push("/help");
        },
        goPage(e) {
            console.log(this.$t("language.title"));
            this.$router.push(e);
            if (e == "/problem") {
                this.$store.commit("setIndex", 1);
            }
            if (e == "/phone") {
                this.$store.commit("setIndex", 3);
            }
        },
        openPage() {
            // window.open(this.$config.orgin, "_bank");
            this.$http.get(this.$api.discountDomain).then((res) => {
                if (res.code == 0) {
                    window.open(res.data.domain, "_bank");
                }
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.home {
    .content {
        margin: 10px 18%;

        .ul {
            background: #fff;
            padding: 24px 70px;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            .flexList {
                display: flex;
                align-items: center;
                width: 50%;
                margin-bottom: 35px;
                flex-shrink: 1;
                .item {
                    color: #2d2b4d;
                    font-size: 18px;
                    font-weight: 500;
                    width: 90px;
                    flex-shrink: 0;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                    letter-spacing: 0;
                    text-overflow: ellipsis;
                }
                .right {
                    flex-grow: 2;
                    margin-left: 50px;
                }
                .listname {
                    margin: 10px;
                    cursor: pointer;
                    color: var(--themeColor);
                    flex-shrink: 1;
                    width: 65%;
                    display: block;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;
                }
            }
        }
        .right1 {
            display: flex;
            justify-content: space-around;
            margin-bottom: 40px;
            .btnDiv {
                text-align: center;
                background: #fff;
                border-radius: 4px;
                width: 440px;
                height: 60px;
                line-height: 60px;
                cursor: pointer;
                .imgHeader {
                    width: 25px;
                    height: 25px;
                }
                .flash {
                    display: inline-block;
                    font-size: 18px;
                    color: #2d2b4d;
                    margin-left: 5px;
                }
            }
            .Breathe {
                animation: flashing 2s infinite linear;
            }
            @keyframes flashing {
                0% {
                    box-shadow: 0 0 10px 1px var(--themeColor);
                }
                50% {
                    box-shadow: none;
                }
                100% {
                    box-shadow: 0 0 10px 1px var(--themeColor);
                }
            }
            .btnDiv:hover {
                animation: none;
                box-shadow: 0 0 10px 1px var(--themeColor);
            }
        }
    }
    .content::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 5px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 1px;
    }
    .content::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 10px;
        background-color: var(--themeColor);
    }
    .content::-webkit-scrollbar-track {
        /*滚动条里面轨道*/
        box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
        background: #f4f4f4;
        border-radius: 10px;
    }
    .title {
        color: #2d2b4d;
        font-size: 16px;
        margin-bottom: 20px;
    }
    .box {
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        padding: 0;
        margin-left: 5px;
        .item {
            background: #fff;
            text-align: center;
            width: 18%;
            min-width: 200px;
            height: 160px;
            line-height: 160px;
            margin-right: 20px;
            margin-bottom: 20px;
            cursor: pointer;
            .itemImg {
                width: 20px;
                height: 20px;
                vertical-align: middle;
            }
            .name {
                color: #1d1717;
                font-size: 18px;
                font-weight: bold;
                vertical-align: middle;
            }
        }
        .item:hover {
            box-shadow: 0 0 10px 1px var(--themeColor);
        }
    }
}
</style>